.tabs {
  position: -webkit-sticky;
  position: sticky;
  top: 20px;
  z-index: 1000;

  @media (max-width: 400px) {
    display: flex;
    flex-wrap: nowrap;
    width: 100%;
    overflow-x: auto;
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    z-index: 1000;
    background-color: white;
  }
}