html, body {
  height: 100%;
  width: 100%;
  display: block;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

#root {
  height: 100%;
  width: 100%;
  display: block;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.container-fluid {
  height: 100%;
  width: 100%;
  display: block;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.title {
  text-align: center;
}

.page {
  height: 100%;
  max-width: 100%;
  padding: 0;
  margin: 0;
  position: relative;
  display: block;
  box-sizing: border-box;

  @media (max-width: 400px) {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
    display: block;
    box-sizing: border-box;
  }
}

.under-title {
  text-align: center;
  color: rgb(48, 48, 48);
}

.error {
  color: red;
}

.top {
  margin-top: 25px;
}

.start-title {
  text-align: start;
}

.padding-10 {
  padding: 10px;
}

.btn-bg {
  width: 100%;
  margin-top: 10px;
}

@media (max-width: 400px) {
  .btn-bg {
    width: 100%;
  }
}

.custom-input {
  opacity: 0;
  z-index: 10;
}

.col .col-sm .col-sm-12 {
  padding-right: 0;
  padding-left: 0;
}

.col {
  padding: 0;
}