.own-dictionary-page {
  display: block;
  height: 100%;
  width: 100%;
}

.option {
  cursor: pointer;
}

.close-part {
  display: none;
}

.show-part {
  display: block;
}

.close {
  display: none;
}

.show {
  display: block;
}

.table-item {
  display: flex;
  flex-direction: column;
}

.tab-item {
  padding: 10px;
  background-color: rgb(233, 233, 233);

  p {
    b {
      color: rgb(94, 138, 77);
    }
    background-color: rgb(199, 199, 199);
    border-radius: 5px;
  }
}

.flex-btn-group {
  display: flex;
}
