.navbar-nav {
  display: flex;
  width: 100%;
  justify-content: space-around;
  font-size: 20px;
  position: relative;
}

.header-fixed {
  width: 100%;
  opacity: 1;
  z-index: 11111;
  position: relative;
}

.edit-panel {
  display: none;
}

.edit-panel.active-edit {
  width: 400px;
  height: 400px;
  background-color: rgb(209, 209, 209);
  position: sticky;
  display: block;
  border-radius: 0px 10px 10px 10px;
  margin-left: 40px;

  @media (max-width: 400px) {
    width: 50%;
    height: 400px;
    background-color: rgb(209, 209, 209);
    position: fixed;
    display: block;
    border-radius: 0px 10px 10px 10px;
    left: -35px;
  }
}

.img-header {
  height: 65px;
  width: 65px;
  border-radius: 50%;
  background-size: cover;

  @media (max-width: 400) {
    height: 60px;
    width: 60px;
    border-radius: 50%;
    background-size: cover;
  }
}